.login-user{
  position: relative;
}

.login-user .login-user-btn {
  position: absolute;
  top : 120%;
  left : -220px;
  width : 300px;
  height : 150px;
  background-color : #fff ;
  border-radius :5px;
  /* z-index: 100 ; */
  visibility : hidden;
  overflow: hidden;
}

.login-user-active{
  visibility: visible !important;
}

.btn-show{
  outline: 2px solid #82ccdd;
}
.login-user-active:before{
  content: "";
  position: fixed;
  width: 100vw;
  height: 100vh;
  top : 60px;
  left:0;
  background-color :#3c6382;
  opacity: 0.4;
  z-index: -200 ;

}

.login-user-active  .dashboard-user:hover{
  background-color :#ced6e0;
}