.container-info{
   width : 100%;
   height : 40px;
   background-color : rgb(147, 227, 192);
   position : absolute;
   left : 0;
   top: 60px;
   z-index: 10;
   display: flex;
   align-items : center;
}

.container-info p{
   /* width : 100%; */
   text-align : center;
   color : rgb(41, 64, 54);
   margin: auto;
}

.container-info i{
   float : right;
   text-align : center;
   font-size : 20px;
   padding : 5px;
   margin-right: 10px;
   color : rgb(41, 64, 54);
   cursor: pointer;
}