.pricing-container {
  background: linear-gradient(to bottom, #93e3c0 50%, #fff 50%);
}

.pricing-card {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 60%;
  margin: 20px auto;
  height: 800px;
  flex-wrap: wrap;

  /* border : 3px solid red; */
}

.ctn-card {
  box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px,
    rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
  height: 90%;
  width: 45%;
  padding: 15px;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  /* align-items : flex-start; */
  flex-direction: column;
  border-radius: 5px;
}

.card-left-up-option {
  display: table;
  position: relative;
  flex: 1;
}

.card-right-up-option {
  display: table;
  position: relative;
  flex: 1;
}

.show-more {
  top: 100%;
  left: 15%;
  cursor: pointer;
  text-decoration: underline;
  color: rgb(51, 51, 51);
  font-weight: italic;
  font-size: 14px;
}

.show-more:hover {
  cursor: pointer;
  color: rgb(38, 153, 251);
}

.pricing-container-midle-title {
  text-align: center;
  font-weight: bold;
  font-size: 20px;
  position: relative;
  width: 60%;
  margin: auto;
  overflow: hidden;
}

.pricing-container-midle-title:before {
  content: "";
  position: absolute;
  width: 50%;
  height: 1px;
  background-color: #ddd;
  top: 50%;
  left: -10%;
}

.pricing-container-midle-title:after {
  content: "";
  position: absolute;
  width: 50%;
  height: 1px;
  background-color: #ddd;
  top: 50%;
  left: 60%;
}

.pricing-container-midle-content {
  width: 80%;
  display: flex;
  justify-content: space-between;
  justify-content: center;
  flex-wrap: wrap;
  margin: auto;
}

.pricing-container-midle-content div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
  margin-bottom: 1rem;
  /* margin :  auto 1rem;; */
}

.pricing-container-midle-content div p {
  width: 80%;
  color: #54616c;
}

.pricing-container-midle-content div i {
  font-size: 30px;
  color: #54616c;
}

.pricing-container-question h3 {
  border-top: 1px solid #ddd;
  width: 60%;
  padding: 20px;
  font-size: 40px;
  margin: auto;
  text-align: center;
  color: #54616c;
}

.pricing-container-question .pricing-container-question-content {
  width: 60%;
  margin: 2rem auto;
}

.pricing-container-bottom {
  border-top: 1px solid #eee;
  width: 60%;
  margin: 2rem auto;

}

.pricing-container-bottom-card {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 300px;
    width :700px;
    margin : auto;
}
.pricing-container-profile{
  display: flex;
  align-items: center;
  justify-content: center;
  /* flex : 1; */
}

.pricing-container-bottom-card button{

    width: 120px;
    height: 40px;
    padding: 3px;
    color: #2699fb;
    border: none;
    outline : 1px solid #2699fb;
    border-radius: 3px;
    font-size: 16px;

}
.pricing-container-bottom-card button:hover{
  background-color:#2699fb;
  color : #fff;
  cursor: pointer;
}