.container-upload{
   display : flex;
   flex-direction : column;
   justify-content : space-evenly;
   align-items : center;
   width : 60%;
   margin : auto;
   padding : 10px;
}

.container-upload .upload{
   /* background-color: #f1f2f6; */
   display : flex;
   justify-content :center;
   padding : 1rem;
   align-items : center;
   border-radius : 5px;
   margin-bottom: 2rem;
   width : 200px;
   height : 40px;
}

.upload-show{
   width : 60%;
   /* display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center; */
   margin : 2rem auto;
}
.upload-show .upload-btn{
   width: 80%;
   height: auto;
   display: flex;
   justify-content: center;
   align-items: center;
   padding : 1rem;
   margin: auto;
}
.display-container{
   min-height :400px;
   display : flex;
   justify-content : center;
   align-items:center;
   background-color: #f7f9fa;
   width : 60%;
   margin : 2rem auto;
}

