.dashboard-container{
   display:flex;
   flex-direction: column;
   justify-content: flex-start;
   align-items: center;
   background: linear-gradient(to bottom, #93e3c0 50%, #fff 50%);
}

.dashboard-container .dashboard-content{
   width : 900px;
   min-height : 260px;
   box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgb(209, 213, 219) 0px 0px 0px 1px inset;
   margin-top: 2rem;
   margin-bottom:2rem;
   background-color: #fff;
}

.dashboard-container .dashboard-content ul{
   display:flex;
   /* justify-content:space-evenly;
   align-items:center; */
   background-color: #edeff0;
   flex-wrap: wrap;
}

.dashboard-container .dashboard-content ul  .nav-list-items{
   list-style-type: none;
   padding : 10px;
   flex : 1;
   text-align: center;
   font-size:16px;
}

.dashboard-container .dashboard-content ul .nav-list-items:hover{
  background-color:  #dfe4ea;
  cursor: pointer;
}

.navlink-selected{
   background-color: #fff;
}