.container-liveshow {
  margin: 0 auto;
}
.container-liveshow h2{
   color : rgb(69,69,69);
   font-size :30px;
   margin: 0 auto;
   text-align: center;
   padding-top : 2rem;
}
.container-liveshow ul{
   list-style-type:none;
   width : 60%;
   display: flex;
   align-items:center;
   margin-bottom: 1.5rem;
   flex-wrap: wrap;
   margin : 1rem auto;
   padding-top : 5px;
   padding-bottom : 5px;

}

.container-liveshow ul li{
   cursor: pointer;
   flex : 1;
   color : rgb(69,69,69)
   

}
.conatiner-img-live{
   width :80%;
   margin : 0 auto;
   display: flex;
   flex-direction: row;
   justify-content:space-between;
   align-items:center;
   flex-wrap: wrap;
}



.ctn-img-1,.ctn-img-2{
   position: relative;
}

.ctn-img-1 img:nth-child(2){
   position: absolute;
   top : 50%;
   left : 75%;
   background-image: url(../assets/images/background.png);
}

.ctn-img-1 svg{
   position: absolute;
   top : 100%;
   left : 30%;
   width : 50px; 
   height : 50px;
   color :rgb(84, 97, 108);
}

.cnt-img-middle{
   position: relative;
}
.cnt-img-middle svg{
   position: absolute;
   top : 10%;
   left : -30%;
   width : 50px; 
   height : 50px;
   color :rgb(84, 97, 108);
   transform : rotate(180deg) scaleX(-1);
}
.ctn-img-2 img:nth-child(1){
   position: absolute;
   top : 50%;
   left : -75%;
}

.active-live {
   color: rgb(38, 153, 251);
   border-bottom: 3px solid rgb(38, 153, 251);
 }
 
 @media only screen and (max-width: 1400px) {
   .conatiner-img-live {
     flex-direction: column;
     justify-content : flex-start;
   }

   .ctn-img-1 ,  .ctn-img-2{
      display: flex;
      flex-direction: column;
   }
   .ctn-img-1 img:nth-child(2){
      position: relative;
      top : 0;
      left :0;
   }
   .ctn-img-2 img:nth-child(1){
      position: relative;
      top : 0;
      left :0;
   }

 }