.checkouts-container{
   display : grid ;
   grid-template-columns: repeat(3,1fr);
   height : 100vh;
}

.checkouts-container-left{
   grid-column: 1/3;
   display:flex;
/*   
   flex-direction: row; 
   justify-content: flex-start;
   align-items: flex-start;
   align-items: center; */
   text-align: center;
  

}

.checkouts-container-right{
   grid-column: 3 / 4;
   background-color: rgb(247, 249, 250);
   border-left: 1px solid #eee;

}

.checkouts-container-right div{
   display : flex;
   justify-content: space-between;
   align-items: center;
   width : 90%;
   margin-left: auto;
   margin-right: auto;
   /* margin-bottom: 3rem; */
}

.checkouts-container-bottom  {
   
   display : flex;
   flex-direction: column;
   justify-content: flex-start;
   align-items: center;
   margin-top: 2rem;
}
.checkouts-container-bottom div{
   display : flex;
   flex-direction: row;
   flex-direction: flex-start;
   margin-bottom: 5px;

}

.checkouts-container-bottom div p {
   margin-right: auto;
}

.checkouts-container-bottom div i {
   margin-right: 10px;
   font-size: 24px;
   color :rgb(147, 227, 192)
}