.display{
   background-color: #fff;
   /* min-height: 800px; */
   width : 100%;
   box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
   padding : 1rem;
}

.display-title {
   list-style: none;
   display: flex;
   flex-direction: row;
   justify-content: space-between;
   align-items:center;
   
}
.display-nav{
   display: flex;
   flex-direction: row;
   justify-content: space-between;
   width : 40%;
   padding : 0.5rem;

}

.icon{
   margin-top: 0.3rem;
   margin-right: 0.6rem;
   color : #ccc;
   font-size:1.4rem;
   cursor: pointer;;
}

.display-nav div{
   cursor: pointer;

}

.display-img {
   display: flex;
   /* justify-content: space-between; */

   align-items: center;
}


.display-img .ctn-img{
   /* width: 66%; */
   flex : 3;
   /* flex-basis: 66%;  */
   /* width: 66%; */
   /* object-fit: cover; */
   /* height: 60%; */
   
} 
.ctn-img img{
   object-fit:cover;
   background-image : url(../assets/images/background.png);
}
.display-img .display-download{
   flex: 1;
   display: flex;
   flex-direction: column;
   margin-left:1rem;
   gap: 1rem;
}

.display-img .display-download button{
   height : 40px;
   min-width: 60px;
   background-color: #2699fb;
   border: none;
   outline: #2699fb;
   font-weight: bold;
   color : #fff;
   border-radius: 5px;
   cursor: pointer;
}
.display-img .display-download  p{
   color : rgb(116, 116, 116);
   font-size : 14px;
   font-style: italic;
}
.active-display {
   color: rgb(38, 153, 251);
   border-bottom: 3px solid rgb(38, 153, 251);
   font-weight: bold;
 }
