.user-container {
  width: 80%;
  margin: 2rem auto;
  /* background-color: #fff; */
}

.user-container h1{
   text-align: center;
   margin : 0.5rem auto;
   font-size:40px;
   color : rgb(69, 69, 69);

}

.user-container p{
   text-align: center;
   margin-bottom : 1rem ;
   font-size:20px;
   color : rgb(69, 69, 69);

}
.user-container-imgs {
  width: 80%;
  /* border: 1px solid black; */
  height: auto;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin: auto;
  background-color:#ffff;

  /* justify-content:space-between; */
}
.user-container-imgs div {
  /* flex: 1; */
  display:flex;

}

.user-container-imgs div div {
  flex :1 0 45%;
  margin : 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;


}
.src-container img{
  flex : 1;
  width : 100%;
  height : 100%;
  object-fit: contain;
}

.pre-container img{
  background-image: url(../assets/images/background.png);
  flex : 1;
  width : 100%;
  height : 100%;
  object-fit: contain;
}

.src-container button{
  margin-top:10px;
  padding  : 5px;
  width : 100px;
  height : 40px;
  background-color: rgb(38, 153, 251);
  border : none;
  color : #fff;
  border-radius : 5px;
  opacity: 0.9;
  font-weight: 500;
}

.pre-container button{
  margin-top:10px;
  padding  : 5px;
  width : 100px;
  height : 40px;
  background-color: rgb(38, 153, 251);
  border : none;
  color : #fff;
  border-radius : 5px;
  opacity: 0.9;
  font-weight: 500;
}

.src-container button:hover{
  cursor: pointer;
  opacity: 1;
}

.pre-container button:hover{
  cursor: pointer;
  opacity: 1
}