.plan-container-second{
   display: flex;
   flex-direction: column;
   align-items: center;
   flex : 3;
   margin:  1rem;
}
.plan-container-second h3{
   /* flex : 1; */
   margin: auto;
   text-align: center;
   color : rgb(69, 69, 69);
   padding : 10px;
   font-size: 24px;
}
.plan-container-second > div{
   flex : 1;
   display: flex;
   align-items:center;
   width : 100%;
}
.plan-container-second > div div{
   flex : 1;
   display: flex;
   flex-direction: column;
   align-items: center;
  
}
.plan-container-second h5{
   color : #2699FB;
   font-size :18px;
}

.plan-container-third{
   flex : 2;
   display: flex;
   align-items: center;
   flex-direction: column;
   width : 100%;
}
.plan-container-third  div{
   display: flex;
   align-items: center;
   justify-content: space-evenly;
   flex :1;
   /* width : 100%; */


}

.plan-container-third h3{
   margin : 1rem;
   color : #54616c;
}
.plan-container-third  div button{
   flex : 1 0;
   min-width : 180px;
   height : 50px;
   color : #54616c;
   /* background-color : #2699FB; */
   margin :  2rem;
   border : none;
   border-radius : 5px;
   padding : 3px;
   font-size: 16px;
   outline : 1px solid #54616c;
   /* opacity: 0.8; */
}

.plan-container-third  div button:hover{
   cursor: pointer;
   background-color: rgb(84, 97, 108);
   color : #fff;
   
}