.login-container{
   width : 50%;
   margin: 2rem auto;
}

.login-form{
   display:flex;
   flex-direction: column;
   justify-content:flex-start;
   max-width : 400px;
   margin: auto;
   align-items: center;
   min-height: 400px;
   box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}

.login-form div ul{
   display: flex;
   flex-direction: row;
   list-style: none;
   justify-content:space-between;
   align-items:center;
   margin:  auto;
   /* width: 100%; */
   
}
.login-form div ul li {
   width : 200px;
   text-align: center;
   cursor: pointer;
   /* background-color:#EDEFF0; */
   padding:10px;
}
.login-email{
   display:flex;
   flex-direction: column;
   justify-content:space-evenly;
   align-items: center;
   height : 300px;
}
.text-break:before{ 
   position: absolute;
   content: "";
   width : 47%;
   height : 1px;
   top : 50%;
   left :0 ;
   background-color : #eee;
   z-index: -999;
   /* margin-right: 10px; */
   /* transform : translateY() */
}


.text-break:after{ 
   position: absolute;
   content: "";
   width :48%;
   height : 1px;
   top : 50%;
   left :53% ;
   background-color : #eee;
   z-index: -999;
   /* transform : translateY() */
}

.login-btn{
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items:center;
}

.login-btn button {
   height : 40px;
   width : 300px;
   margin-top : 1rem;
}
.login-email{
   display: flex;
   flex-direction: column;
   justify-content: space-evenly;
   align-items:center;
}

.login-email input{
   width : 300px;
   height : 40px;
   border : 1px solid #eee;
   border-radius :2px;
}

.container-checkbox{
   display: flex;
   flex-direction: row;
   justify-content: flex-start;
   align-items: center;
   /* width :80%; */
   margin-right: 180px;
}
.active-login{
   background-color : #ffff;
}
 .active-login em{
   
   border-bottom :5px solid rgb(38, 153, 251);
   font-weight: bold;

}

.login-btn fa{
   font-size:30px;
}

.login-btn button {
   display: flex;
   align-items: center;
   justify-content: flex-start;
   font-size:16px;
   text-align: center;
}


.login-btn button:hover {
   cursor: pointer;
   color :rgb(38, 153, 251);

}

.login-email input:focus{
   outline: 2px solid rgb(38, 153, 251);;
}

.signup-form {
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   align-items: center;
   /* margin-bottom: 4rem; */
}

.signup-form label{
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
}


.signup-form input {
   width : 300px;
   height : 40px;
   border : 1px solid #eee;
   border-radius :2px;

}

.signup-form div label{
   margin-left: auto !important;
   display: block;
   margin-bottom: 2px;

}

.sign-bottom{
   display: flex;
   justify-content: flex-start;
   align-items:center;
   margin-right: auto;
}