.footer{
   padding-top: 2rem;
   padding-bottom: 2rem;
   display: flex;
   max-height : 300px;
   flex-direction : row;
   justify-content :space-evenly;
   align-items :flex-start;
   /* border-top : 1px solid #eee; */
   flex-wrap: wrap;
   margin : 2rem auto;
   color : rgb(69,69,69);
   border-top: 1px solid #ddd;
}


.footer div ul{
   display: flex;
   flex-direction : column;
   align-items :start;
}

.footer div ul li{
   list-style: none;
   cursor: pointer;
}

.footer div ul li:first-child{
   margin-bottom: 1rem;
}