.header-container {
  /* position:fixed */
  display: flex;
  justify-content: space-between;
  min-height: 60px;
  background-color: #ffff;
  align-items: center;
  flex-wrap: wrap;
  position:sticky;
  /* right : 0;
  left : 0; */
  top :0;
  /* width :100vw; */
  z-index: 100;
  /* position :fixed; */
}

.header-container .header-left ul {
  list-style-type: none;
  min-width: 600px;
  display: flex;
  /* justify-content: space-between; */
  flex-wrap: wrap;
  align-items: center;
  padding: 0.5rem;
  font-weight: 500;
}
.header-container .header-left ul li {
  margin-right: auto;
  flex : 1;
  text-align: center;
  opacity: 0.8;
}

.header-container .header-left ul li:hover {
  /* border-bottom: 1px solid rgb(84, 97, 108, 0.5); */
  opacity : 1;
}

/* .header-container .header-left ul li:nth-child(1):hover {
  border-bottom: none !important;
} */
.header-container .header-left ul li img {
  cursor: pointer;
  min-width: 150px;
  height: 25px;
  margin-left: 1rem;
}

.header-container .header-right {
  list-style-type: none;
  width: 400px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  min-height: 50px;
  flex-wrap: wrap;
}
.header-right button {
  height: 65%;
  display: block;
  padding: 0.25rem;
  cursor: pointer;
  margin: auto;
  font-weight: 500;
  font-size: 16px;
  /* letter-spacing:0.5px; */
}
.header-right button:nth-child(1) {
  border: 1px solid rgb(38, 153, 251);
  min-width: 45%;
  border-radius: 5px;
  color: rgb(38, 153, 251);
}

.header-right button:nth-child(2) {
  border: 1px solid rgb(84, 97, 108);
  border-radius: 5px;
  min-width: 20%;
}
.header-right button:nth-child(1):hover {
  color: #fff;
  background-color: rgb(38, 153, 251);
}

.header-right .sign-in-btn:hover {
  color: #fff;
  background-color: rgb(84, 97, 108);
}


@media only screen and (max-width: 760) {
  .header-container {
    flex-direction: column;
  }
}
